import React, { useState } from "react";
import { Layout, Row, Col, Divider, Button } from "antd";
import { LoggedinHeader } from "../../containers";
import Buttons from "../button/Button";
import "./hotlineDirectory.scss";
import CreateHotline from "../../assets/images/create-hotline.svg";
import { Link } from "react-router-dom";
import HotlineCreation from "../../components/hotlineCreation/HotlineCreation";
import OrgSidebar from "../../containers/OrgSidebar";

const { Content } = Layout;

const HotlineDirectory = () => {
  const [showResult, setShowResult] = React.useState(false);

  const showBtn = () => {
    setShowResult(true);
  } 
  return (
 
       <React.Fragment>
      {!showResult === true ? <React.Fragment> <Row className="inner-header">
      <LoggedinHeader amORGUser/>
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
          <OrgSidebar amORGUser/>
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">Hotline Directory</h1>
              </Row>
              <div className="mid-content d-flex justify-content-center align-items-center">
                <Row>
                  <Col span={24}>
                    <div className="c-vector-area">
                      <img
                        src={CreateHotline}
                        alt="Create Hotline"
                        className="pr-4"
                      />
                    </div>
                    <p className="text-center pt-3">
                      Create Hotline entries and assign them to managers,
                      <br />
                      head of departments or leadership.
                    </p>
                    <Row className="d-flex justify-content-center">
                      <Divider className="mt-1" />
                    </Row>
                    <Col
                      span={24}
                      className="d-flex align-items-center justify-content-center"
                    >
                       <Button onClick={showBtn} type="primary">Create</Button>
                      {/* <Link to="/hotline">
                        <Buttons value="Create" />
                     </Link> */}
                    </Col>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
        </Row></React.Fragment> : <HotlineCreation />}

</React.Fragment>
  );
};

export default HotlineDirectory;
