import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import ProtectedRoute from "./ProtectedRoute";
import ManageOrg from "./components/manageOrg/ManageOrg";
import ManageEmp from "./components/manageEmp/ManageEmp";
import Login from "./components/login/AmccLogin";
import OrgLogin from "./components/login/OrgccLogin";
import AmWebLogin from "./components/login/AmWebLogin";
import HotlineCreation from "./components/hotlineCreation/HotlineCreation";
import HotlineContainer from "./components/hotlineDirectory/HotlineContainer"; 
import AssignHotlineModal from "./components/modal/AssignHotlineModal";
import HotlineMessages from "./components/hotlineMessages/HotlineMessages";
import PermissionDenied from "./containers/PermissionDenied";
import * as AppStorage from "../src/utils/AppStorage";
//fonts
import Poppins from "./components/hotlineMessages/Poppins";
import SFPro from "./components/hotlineMessages/SFPro";
import Sofia from "./components/hotlineMessages/Sofia";

function Routes(props) {
  const isAuthenticated = AppStorage.get("user");
  const commonForAllUsers = JSON.parse(AppStorage.get("user"));

  let isamcc = false;
  let isorgcc = false;
  let isamweb = false;
  if (!isAuthenticated) {
    isamcc = true;
    isorgcc = true;
    isamweb = true;
  }
  if (
    isAuthenticated &&
    commonForAllUsers &&
    commonForAllUsers.user_data != "null"
  ) {
    isamcc = commonForAllUsers.user_data.is_amcc_user;
    isorgcc = commonForAllUsers.user_data.is_org_cc_user;
    isamweb = commonForAllUsers.user_data.is_am_web_user;
  }
  const url = window.location.pathname;

  return (
    <BrowserRouter>
      <Switch>
        <Route path="/amcc" component={Login} />
        <Route path="/orgcc" component={OrgLogin} />
        <Route path="/amweb" component={AmWebLogin} />
        <Route path="/poppinsmob" component={Poppins} />
        <Route path="/sfpromob" component={SFPro} />
        <Route path="/sofiamob" component={Sofia} />
        {isamweb && (url === "/hotlinemessages" || url === "/amweb") ? (
          <>
            {" "}
            {/* AMWEB ROUTING */}
            <ProtectedRoute
              exact={true}
              path="/hotlinemessages"
              amweb
              component={HotlineMessages}
            />
          </>
        ) : isamcc && (url === "/organization" || url === "/amcc") ? (
          <>
            {" "}
            {/* AMCC ROUTING */}
            <ProtectedRoute
              exact={true}
              path="/organization"
              amcc
              component={ManageOrg}
            />{" "}
          </>
        ) : isorgcc &&
          (url === "/hotline" ||
            url === "/employee" ||
            url === "/assignhotline" ||
            url === "/hotlinedirectory" ||
            url === "/orgcc") ? (
          <Switch>
            <ProtectedRoute
              exact={true}
              path="/hotline"
              amorg
              component={HotlineCreation}
            />
            <ProtectedRoute
              exact={true}
              path="/employee"
              amorg
              component={ManageEmp}
            />
            <ProtectedRoute
              exact={true}
              path="/assignhotline"
              amorg
              component={AssignHotlineModal}
            />
            <ProtectedRoute
              exact={true}
              path="/hotlinedirectory"
              amorg
              component={HotlineContainer}
            />
          </Switch>
        ) : (
          isAuthenticated && <Route component={PermissionDenied} />
        )}
      </Switch>
    </BrowserRouter>
  );
}

export default Routes;