import React, { useEffect } from "react";
import HotlineDirectory from "../../components/hotlineDirectory/HotlineDirectory";
import ShowAllHotlines from "../../components/hotlineDirectory/ShowAllHotlines";
import { connect } from "react-redux";
import { getHotlinesAsync } from "../../actions/hotlines";

const HotlineContainer = (props) => {
  useEffect(() => {
    const { dispatch } = props;
    dispatch(getHotlinesAsync());
  }, []);

  return props.hotlinesdata.length > 0 ? (
    <ShowAllHotlines hotlines={props.hotlinesdata} />
  ) : (
    <HotlineDirectory />
  );
};

function mapStateToProps(state) {
  const { hotlinesdata } = state.hotlinedata;
  return {
    hotlinesdata,
  };
}

export default connect(mapStateToProps)(HotlineContainer);