import React from "react";
import "antd/dist/antd.css";
import "./assets/scss/style.scss";
import "./assets/icons/icon-styles.css";
import { Provider, connect } from "react-redux";
import Routes from "./routes";
import store from "./services/stores";
import IdleTimer from "react-idle-timer";
import AuthService from "../src/services/auth-service";
import * as AppStorage from "../src/utils/AppStorage";

function App() {
  const timeOut = 300000;

  const onIdle = (e) => {
    if(AppStorage.get('user')) {
      if(AppStorage.get('userType') === 'Org Control Center') {
        window.location.href = "/orgcc";
      } else if(AppStorage.get('userType') === 'AM WEB') {
        window.location.href = "/amweb";
      } else if(AppStorage.get('userType') === 'AM CC') {
        window.location.href = "/amcc";
      }
      AuthService.logout();
    }
  }
  return (
    <Provider store={store}>
      <IdleTimer
        element={document}
        onIdle={onIdle}
        debounce={250}
        timeout={timeOut}
      />
      <div className="App">
        <div>
          <div className="content">
          <Routes />
          </div>
        </div>
      </div>
    </Provider>
  );
}

export default App;
