import React, { Component } from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Typography
} from "antd";
import { LoggedinHeader } from "../../containers";
import styles from './sofia.module.scss'
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import NoHotlines from "../../assets/images/no-hotlines.svg";
const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;
const { TextArea } = Input;

function log(e) {
  console.log(e);
}

function preventDefault(e) {
  e.preventDefault();
  console.log("Clicked! But prevent default.");
}

class Sofia extends Component {
  render() {
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <AmWebSidebar />
              <Content className="">
                <Row className="py-3">
                  <h1 className= {`mb-0 ${styles.heading_sm}`}>Hotline Messages</h1>
                </Row>
                <div className="mid-content p-0">
                  <Row>
                    <Col
                      sm={24}
                      md={24}
                      lg={6}
                      xl={6}
                      className={`py-4 ${styles.message_section}`}
                    >
                      <Col
                        span={24}
                        className=" d-flex justify-content-between px-3"
                      >
                        <h6>Messages</h6>
                        <FilterPopover />
                      </Col>

                      <Col span={24} className="search-box mt-3 px-3">
                        <Search placeholder="Search" onSearch={this.onSearch} />
                      </Col>
                      <Col span={24} className="mt-2 inbox-messages">
                        <div className="msg-wrapper">
                          <div className="d-flex justify-content-between">
                            <Paragraph ellipsis className="w-75 pr-2 mb-1">
                              I am not allowed to speak in meeting as senior
                              people were involved
                            </Paragraph>

                            <span className="small"> 03:15</span>
                          </div>
                          <div className="d-flex justify-content-between">
                            <Paragraph
                              ellipsis
                              className="font-weight-normal mb-1"
                            >
                              Microagressions | MA67287365
                            </Paragraph>
                          </div>
                        </div>

                        <div className="msg-wrapper pointer">
                          <div className="d-flex justify-content-between">
                            <Paragraph ellipsis className="w-75 pr-2 mb-1">
                            Racial Microagression
                            </Paragraph>

                            <span className="small">04 / 23</span>
                          </div>
                          <div className="d-flex justify-content-between">
                            <Paragraph
                              ellipsis
                              className="font-weight-normal mb-1"
                            >
                              Microagressions | MA67287316
                            </Paragraph>
                          </div>
                        </div>

                        <div className="msg-wrapper">
                          <div className="d-flex justify-content-between">
                            <Paragraph ellipsis className="w-75 pr-2 mb-1">
                            Anything goes after hours
                            </Paragraph>

                            <span className="small"> 04 / 22</span>
                          </div>
                          <div className="d-flex justify-content-between">
                            <Paragraph
                              ellipsis
                              className="font-weight-normal mb-1 ant-typography-sm"
                            >
                             Misconduct | MA67287334
                            </Paragraph>
                          </div>
                        </div>

                        <div className="msg-wrapper">
                          <div className="d-flex justify-content-between">
                            <Paragraph ellipsis className="w-75 pr-2 mb-1">
                            Indecent behavior
                            </Paragraph>

                            <span className="small"> 04 / 22</span>
                          </div>
                          <div className="d-flex justify-content-between">
                            <Paragraph
                              ellipsis
                              className="font-weight-normal mb-1 ant-typography-sm"
                            >
                              Sexual Harassment | MA6728735
                            </Paragraph>
                          </div>

                        </div>
                      </Col>
                    </Col>

                    <Col
                      span={18}
                      className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                    >
                      <Row className="justify-content-center my-3">
                        <img
                          src={NoHotlines}
                          alt="No Hotlines Selected"
                          style={{ opacity: "0.8" }}
                        />
                      </Row>
                      <h5>No Hotline Selected</h5>
                      <small>Select a hotline to view</small>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default Sofia;
