import React, { Component } from "react";
import { Modal } from "antd";
import "../modal/modal.scss";

class DeleteModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <>
        {/* onClick={this.showModal} */}
        <a href="#">Delete</a>
        <Modal
          title="Delete User"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
          }}
          okText="Yes"
          cancelText="No"
        >
          <p>Are you sure you want to delete Sophia Joseph from your users?</p>
        </Modal>
      </>
    );
  }
}

export default DeleteModal;
