import React, { useState } from "react";
import { Layout, Steps, Row, Col, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import "../hotlineCreation/hotlineCreation.scss";
import CreateHotlineStepOne from "../form/CreateHotlineStepOne";
import CreateHotlineStepTwo from "../form/CreateHotlineStepTwo";
import CreateHotlineStepThree from "../form/CreateHotlineStepThree";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { NavLink } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";



const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: "Add Details",
    content: "First-content",
  },
  {
    title: "Assign Employee",
    content: "Second-content",
  },
  {
    title: "Create Directory",
    content: "Last-content",
  },
];

const HotlineCreation = () => {
  const [current, setCurrent] = useState(0);
  const [stepOneHotLine, setStepOneHotLine] = useState({
    directoryName: "",
    description: "",
    status: "",
    sDate: "",
    tDate: "",
    hotlineIconId: "",
    iconHtml: "",
  });
  const [stepTwoHotLine, setStepTwoHotLine] = useState({
    selectedRows: "",
    selectedRowKeys: [],
    chekedValues: [],
  });

  const [permissions, setPermissions] = useState([]);
 
  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const handleStepOneOnChange = (e) => {
    setStepOneHotLine({ ...stepOneHotLine, [e.target.id]: e.target.value });
  };

  const handleStepOneOnSelect = (value, name) => {
    setStepOneHotLine({ ...stepOneHotLine, [name]: value });
  };

  const handleStepOneDatePickerChange = (date, name) => {
    setStepOneHotLine({ ...stepOneHotLine, [name]: date });
  };

  const addIcon = (values, id) => {
    setStepOneHotLine({
      ...stepOneHotLine,
      hotlineIconId: id,
      iconHtml: values,
    });
  };

  const getIconText = (id) => {
    switch (id) {
      case "1":
        return "Sexual Harassment";
      case "2":
        return "Voilence";
      case "3":
        return "Racism";
      case "4":
        return "Cyber Bullying";
      case "5":
        return "Fight";
      case "6":
        return "Discrimination";
      case "7":
        return "Slap";
      default:
        return "";
    }
  };

  const handleStepTwoOnSelect = (selectedRowKeys, selectedRows) => {
    if (selectedRows > stepTwoHotLine.selectedRows) {
      const user = JSON.parse(AppStorage.get("user"));
      let difference = selectedRows.filter(
        ({ user_id: id1 }) =>
          !permissions.some(({ user_id: id2 }) => id2 === id1)
      );

      let per = difference.reduce((accumulator, currentValue) => {
        accumulator.push({
          user_id: currentValue.user_id,
          can_respond: 1,
          can_view_reports: 0,
          created_by: user.user_details.org_user_id,
        });
        return accumulator;
      }, []);
      setPermissions(permissions.concat(per));
    } else {
      if (selectedRows.length>0) {
        let difference = permissions.filter(
          ({ user_id: id1 }) =>
            !selectedRows.some(({ user_id: id2 }) => id2 === id1)
        );
        let index = permissions.findIndex(
          (p) => p.user_id === difference[0].user_id
        );
        permissions.splice(index, 1);
        setPermissions(permissions);
      } else {
        setPermissions([]);
      }
    }

    setStepTwoHotLine({
      ...stepTwoHotLine,
      selectedRowKeys: selectedRowKeys,
      selectedRows: selectedRows,
    });
  };

  const handleStepTwoCheckValues = (iteam) => {
    let index = permissions.findIndex((el) => el.user_id === iteam.user_id);
    permissions[index] = {
      ...permissions[index],
      can_respond: permissions[index].can_respond === 1 ? 0 : 1,
    };
    setPermissions([...permissions]);
  };

  const getCheckValues = () => {
    setStepTwoHotLine({
      ...stepTwoHotLine,
      chekedValues: permissions,
    });
  };

  const createHotLine = async () => {
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();

    const data = {
      create_hotline: {
        name: stepOneHotLine.directoryName,
        help_text: stepOneHotLine.description,
        start_date: stepOneHotLine.sDate[0]
          ? moment(stepOneHotLine.sDate[0]).format("DD-MM-YYYY")
          : "",
        end_date: stepOneHotLine.sDate[1]
          ? moment(stepOneHotLine.sDate[1]).format("DD-MM-YYYY")
          : "",
        status_id: stepOneHotLine.status,
        hotline_icon_id: parseInt(stepOneHotLine.hotlineIconId),
        created_by: user.user_details.org_user_id,
      },
      permissions: stepTwoHotLine.chekedValues,
    };

    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .post(BASE_URL+"hotline/", data, options)
      .then(() => {
        window.location = "/hotlinedirectory";
      });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser />
            <Content className="">
              <Row className="pt-3">
                <Breadcrumb separator=">">
                  <NavLink to="/hotlinedirectory">
                    <Breadcrumb.Item>Hotline Directory</Breadcrumb.Item>
                  </NavLink>
                  <Breadcrumb.Item>Create Hotline Directory</Breadcrumb.Item>
                </Breadcrumb>
              </Row>
              <Row className="py-3">
                <h1 className="mb-0">Create Hotline Directory Entries</h1>
              </Row>
              <Steps className="mt-2" current={current}>
                {steps.map((item) => (
                  <Step key={item.title} title={item.title} />
                ))}
              </Steps>
              <div className="mid-content mt-4">
                <Row>
                  <Col span={24}>
                    <div className="steps-content">
                      {current == 0 ? (
                        <CreateHotlineStepOne
                          next={next}
                          stepOneHotLineInfo={stepOneHotLine}
                          handleStepOneOnChange={handleStepOneOnChange}
                          handleStepOneOnSelect={handleStepOneOnSelect}
                          handleStepOneDatePickerChange={
                            handleStepOneDatePickerChange
                          }
                          addIcon={addIcon}
                          getIconText={getIconText}
                        />
                      ) : current == 1 ? (
                        <CreateHotlineStepTwo
                          next={next}
                          prev={prev}
                          stepOneHotLine={stepOneHotLine}
                          stepTwoHotLine={stepTwoHotLine}
                          handleStepTwoOnSelect={handleStepTwoOnSelect}
                          handleStepTwoCheckValues={handleStepTwoCheckValues}
                          permissions={permissions}
                          getCheckValues={getCheckValues}
                        />
                      ) : current == 2 ? (
                        <CreateHotlineStepThree
                          prev={prev}
                          createNewHotLine={createHotLine}
                          stepOneHotLine={stepOneHotLine}
                          stepTwoHotLine={stepTwoHotLine}
                          getIconText={getIconText}
                        />
                      ) : (
                        ""
                      )}
                    </div>
                  </Col>
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default HotlineCreation;
