import React from "react";
import { Redirect } from "react-router-dom";
import * as AppStorage from "../src/utils/AppStorage";

class ProtectedRoute extends React.Component {
  render() {
    const Component = this.props.component;
  
  
   
   
    const user = AppStorage.get("user");
    let redirectPath = "/";
    if (this.props.amweb) {
      redirectPath = "/amweb";
    } else if (this.props.amcc) {
      redirectPath = "/amcc";
    }else if (this.props.amorg) {
      redirectPath = "/orgcc";
    }

    return user ? <Component {...this.props}/> : <Redirect to={{ pathname: redirectPath }} />;
  }
}

export default ProtectedRoute;