import React, { useState } from "react";
import { Form, Select, Input, Button, DatePicker, Alert } from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import ReactHtmlParser from "react-html-parser";
import moment from "moment";
import authHeader from "../../services/auth-header";
import axios from "axios";

import "../form/form.scss";
import { BASE_URL } from "../../userconfig";
const { TextArea } = Input;
const { Option } = Select;

const CreateHotlineStepOne = ({
  next,
  stepOneHotLineInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  addIcon,
  getIconText,
}) => {
  const [hotlineForm, setHotlineForm] = useState({
    iconText: "",
  });

  const [error, showError] = useState(false);
  const [disabledbtn, setdisabledbtn] = useState(true);
  const [form] = Form.useForm();
  const [hotlineErrorUpdated, sethotlineErrorUpdated] = useState("");

  const stepOneNext = (e) => {
    e.preventDefault();
    if (
      stepOneHotLineInfo.directoryName &&
      stepOneHotLineInfo.description &&
      stepOneHotLineInfo.iconHtml &&
      stepOneHotLineInfo.status &&
      stepOneHotLineInfo.sDate
    ) {
      showError(false);
      setdisabledbtn(false);
      next();
    } else {
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const CheckHotlineName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL+"checkhotline/",
        { hotline_name: stepOneHotLineInfo.directoryName },
        options
      )
      .then((res) => {
        if (res.data) {
          sethotlineErrorUpdated(res.data[0]);
        }
      });
  };

  const { RangePicker } = DatePicker;

  return (
    <Form className="input-md w-75" name="validate_other" form={form}>
      {error && (
        <Form.Item>
          <Alert
            message="Please fill the fields marked with asterisk(*)"
            type="error"
            showIcon
          ></Alert>
        </Form.Item>
      )}

      <Form.Item
        className="required"
        name="directoryName"
        rules={[
          {
            required: true,
            message: "Please enter directory name",
          },
        ]}
      >
        <TextArea
          showCount
          maxLength={80}
          value={stepOneHotLineInfo.directoryName}
          onChange={(e) => handleStepOneOnChange(e)}
          onBlur={(e) => CheckHotlineName(e)}
          id={"directoryName"}
          className="input-control textarea-input"
          placeholder="Directory Name"
          htmlFor="directoryName"
        />
        {hotlineErrorUpdated == "status: true" && (
          <Alert
            message="Sorry! This hotline already exists in the system. Please use a different name."
            type="error"
            showIcon
          ></Alert>
        )}
        {hotlineErrorUpdated == "status: false" && (
          <Alert
            message="Hotline name is validated."
            type="success"
            showIcon
          ></Alert>
        )}
      </Form.Item>
      <Form.Item
        className="required"
        name="description"
        rules={[
          {
            required: true,
            message: "Please enter description",
          },
        ]}
      >
        {/* {hotlineForm.description.length<15 &&
        <Alert message="Please enter more the 15 characters in the description below" type="info"  showIcon></Alert>
        } */}
        <>
          <TextArea
            className="input-control textarea-input"
            value={stepOneHotLineInfo.description}
            onChange={(e) => handleStepOneOnChange(e)}
            id={"description"}
            placeholder="Description"
            htmlFor="description"
          />
        </>
      </Form.Item>

      <Form.Item className="input-control datepicker-wrapper">
        <RangePicker
          className="datepicker w-50 mr-5 required"
          name="sDate"
          value={stepOneHotLineInfo.sDate}
          onChange={(date) => handleStepOneDatePickerChange(date, "sDate")}
          disabledDate={disabledDate}
          rules={[
            {
              required: true,
              message: "Please select start date",
            },
          ]}
        />
      </Form.Item>
      <Form.Item
        className="required"
        name="status"
        rules={[
          {
            required: true,
            message: "Please select status",
          },
        ]}
      >
        <>
          <Select
            placeholder="Status"
            className="input-control w-50"
            value={stepOneHotLineInfo.status}
            htmlFor="status"
            onSelect={(value) => handleStepOneOnSelect(value, "status")}
          >
            <Option value="" disabled>
              Status
            </Option>
            <Option value="5">Active</Option>
            <Option value="4">Inactive</Option>
          </Select>
        </>
      </Form.Item>
      <Form.Item className="">
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
        >
          <AddIcoModal addIcon={addIcon} />
          {getIconText(stepOneHotLineInfo.hotlineIconId) && (
            <div className="ant-upload-list ant-upload-list-picture">
              <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
                <div className="ant-upload-list-item-info d-inline-flex w-100">
                  <span className="d-inline-flex align-items-center">
                    <a className="ant-upload-list-item-thumbnail">
                      {ReactHtmlParser(stepOneHotLineInfo.iconHtml)}
                    </a>

                    <span
                      className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                      title={getIconText(stepOneHotLineInfo.hotlineIconId)}
                    >
                      {getIconText(stepOneHotLineInfo.hotlineIconId)}
                    </span>
                  </span>
                </div>
              </div>
            </div>
          )}
        </Form.Item>
      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" onClick={stepOneNext}>
          Next
        </Button>
      </Form.Item>
    </Form>
  );
};
export default CreateHotlineStepOne;
