import React from "react";
import { Popover } from "antd";
import DeleteModal from "../../components/modal/DeleteModal";
import dotsicon from "../../assets/icons/dots-icon.svg";

const content = (
  <div>
    <ul>
      <li>
        {" "}
        <a href="#">Edit</a>
      </li>
      <li>
        <DeleteModal />
      </li>
    </ul>
  </div>
);

class GridPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="ico-grid-dots">
            <img src={dotsicon} alt="DotIcon" className="pr-4" />
          </span>
        </Popover>
      </>
    );
  }
}

export default GridPopover;
