import React from "react";
import { Layout, Row, Col, Input, Select } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import GridPopover from "../table/Popover";
import { Link } from "react-router-dom";
import PermissionModal from "./../modal/ShowModal";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllHotlines extends React.Component {
  constructor(props) {
    super(props);
    const columns = [
      { title: "Hotline Name", dataIndex: "name", key: "name", width: "22%" },
      {
        title: "Start Date",
        dataIndex: "start_date",
        key: "start_date",
        width: "22%",
      },
      {
        title: "End Date",
        dataIndex: "end_date",
        key: "end_date",
        width: "21%",
      },
      //{ title: "Permissions", dataIndex: "", key: "", width: "20%" },
      {
        title: "Permissions",
        dataIndex: "",
        key: "x",
        width: "20%",
        render: (selectedRows) => (
          <div>
            <PermissionModal selectedRows={selectedRows} />
          </div>
        ),
        
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) =>
          record.status_id === 5 ? (
            <div className="primary-tag-rounded tag">
              <span className="circle mr-1"></span>Active
            </div>
          ) : (
            <div className="secondary-tag-rounded tag">Inactive</div>
          ),
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: () => <GridPopover />,
      },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }
  
  componentDidMount() {
    let { hotlines } = this.props;
    hotlines.forEach((abc) => {
      abc.start_date = moment(abc.start_date).utc().local().format("MM / DD / YYYY")
      // abc.start_date = abc.start_date.replaceAll("T00:00:00Z", "");
      abc.end_date = moment(abc.end_date).utc().local().format("MM / DD / YYYY")
     // abc.end_date = abc.end_date.replaceAll("T00:00:00Z", "");
    });
    this.setState({ data: hotlines }, () => {
      this.onSearch("");
    });
  }
  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].includes(searchKey);
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };
  render() {
    return (
      <>
        <Row className="inner-header">
        <LoggedinHeader amORGUser/>
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
            <OrgSidebar amORGUser/>
              <Content className="">
                <Row className="py-3">
                  <h1>Hotline Directory</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3">
                    <Col span={18} className="search-row">
                      <Row>
                        <Col span={9} className="search-box">
                          <Search
                          disabled={true}
                            placeholder="Search"
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/hotline">
                        <Buttons value="Create Hotline" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Grid
                  rowKey="hotline_id"
                  columns={this.state.columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}


export default ShowAllHotlines;
