import React, { Component, useEffect, useState, useRef } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Input,
  Typography,
  Badge,
  Divider,
  Tag,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "./hotlineMessages.scss";
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import Messages from "./Messages";
import { BASE_URL } from "../../userconfig";
import { Scrollbars } from "react-custom-scrollbars";
import * as AppStorage from "../../utils/AppStorage";
import NoHotlines from "../../assets/images/no-hotlines.svg";
import CreateHotline from "../../assets/images/create-hotline.svg";

const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;

const HotlineMessages = () => {
  const [conversations, setConversations] = useState([]);
  const [initialConv, setInitialConv] = useState([]);
  const [urgencyLevelData, setUrgencyLevelData] = useState([]);
  const [urgencyLevelHotlines, setUrgencyLevelHotline] = useState([]);
  const [urgencyLevelStatus, setUrgencyLevelStatus] = useState([]);
  const [removeFilter, setRemoveFilter] = useState({});
  const [conversation, setConversation] = useState("");
  const [urgencyLevel, setUrgencyLevel] = useState("");
  const [status, setStatus] = useState("");
  const [searchString, setSearchString] = useState("");
  const [socket, setSocket] = useState(null);
  const [newState, setNewState] = useState({});
  const [unReadCount, setunReadCount] = useState(0);

  const webSocket = useRef(null);
  const [form] = Form.useForm();

  useEffect(async () => {
    getConversations();
    connect();
  }, []);

  const connect = () => {
    webSocket.current = new WebSocket(
      "ws://54.241.83.181:8000/ws/hotlineMessages/"
    );
    webSocket.current.onopen = () => {
      setSocket(webSocket.current);
    };
    webSocket.current.onmessage = (event) => {
      let newConvo = JSON.parse(event.data);
      console.log("WEBSOCKET MESSAGE SENT!!", newConvo);
      setNewState(newConvo);
      socketMethod(newConvo);
    };

    webSocket.current.onclose = () => {
      console.log("socket connection disconnected HOTLINEMESSAGES ");
    };
    webSocket.current.onerror = (error) => {
      console.log("socket connection error HOTLINEMESSAGES ", error);
    };
  };

  const socketMethod = (newConvo) => {
    const user = JSON.parse(AppStorage.get("user"));
    let urgencyState = [];
    let statusState = [];
    let hotlineState = [];
    // only show conversations which are assigned to the user.
    if (newConvo.accessed_users.includes(user.user_data.user_id)) {
      setConversations((conversations) => {
        setUrgencyLevelData((urgencyLevelData) => {
          urgencyState = Object.values(urgencyLevelData)?.map(
            (data) => data.value
          );
          return urgencyLevelData;
        });
        setUrgencyLevelStatus((urgencyLevelStatus) => {
          statusState = Object.values(urgencyLevelStatus)?.map(
            (data) => data.value
          );
          return urgencyLevelStatus;
        });
        setUrgencyLevelHotline((urgencyLevelHotlines) => {
          hotlineState = Object.values(urgencyLevelHotlines)?.map(
            (data) => data.value
          );
          return urgencyLevelHotlines;
        });
        // extracts IDs of all the present conversations for an AMWeb user.
        let conversationIds = [];
        conversations.map(({ conversation_id }) => {
          let convoID = "";
          if (conversation_id.includes("</mark>")) {
            convoID = conversation_id.toString().replace(/(<([^>]+)>)/gi, "");
          } else {
            convoID = conversation_id;
          }
          conversationIds.push(convoID);
        });
        let updatedCount = newConvo;
        let totalConversations = conversations;
        let urgencyFilter = false;
        let statusFilter = false;
        let hotlineFilter = false;
        if (urgencyState.length > 0) {
          urgencyFilter = true;
        }
        if (statusState.length > 0) {
          statusFilter = true;
        }
        if (hotlineState.length > 0) {
          hotlineFilter = true;
        }
        // check if a new conversation is initiated from a mobile App user which is not already present in AMWeb.
        // Also check for already applied filters.
        if (urgencyFilter) {
          totalConversations = filterSocketUrgency(
            urgencyState,
            newConvo,
            totalConversations,
            conversationIds
          );
        } else if (statusFilter) {
          totalConversations = filterSocketStatus(
            statusState,
            newConvo,
            totalConversations,
            conversationIds
          );
        } else if (hotlineFilter) {
          totalConversations = filterSocketHotline(
            hotlineState,
            newConvo,
            totalConversations,
            conversationIds
          );
        } else {
          if (!conversationIds.includes(newConvo.conversation_id)) {
            totalConversations = [...conversations, newConvo];
          } else {
            totalConversations = conversations;
          }
        }

        const updatedArray = totalConversations.map((x) => {
          // check if the message has been sent in a conversation via a different AMWeb user.
          // if yes, then increase the count for that particular conversation ID.
          if (
            x.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
              newConvo.conversation_id &&
            newConvo.created_by !== user.user_data.user_id
          ) {
            if (x.responder_conversation_name.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                responder_conversation_name: x.responder_conversation_name,
              };
            }
            if (x.conversation_id.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                conversation_id: x.conversation_id,
              };
            }
            if (x.hotline.name.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                hotline: {
                  name: x.hotline.name,
                },
              };
            }
            updatedCount = {
              ...updatedCount,
              unread_count: x.unread_count + 1,
            };
            return updatedCount;
          } else if (
            x.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
              newConvo.conversation_id &&
            newConvo.created_by === user.user_data.user_id
          ) {
            if (x.responder_conversation_name.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                responder_conversation_name: x.responder_conversation_name,
              };
            }
            if (x.conversation_id.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                conversation_id: x.conversation_id,
              };
            }
            if (x.hotline.name.includes("</mark>")) {
              updatedCount = {
                ...updatedCount,
                hotline: {
                  name: x.hotline.name,
                },
              };
            }
            return updatedCount;
          } else {
            return x;
          }
        });

        const messageSentConvo = totalConversations.filter(
          (filter) =>
            filter?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
              newConvo?.conversation_id &&
            newConvo.created_by !== user.user_data.user_id
        );

        const newArray = totalConversations.filter(
          (ele) => ele?.conversation_id === newConvo?.conversation_id
        );

        // set the overall count for all the unread conversations.
        for (var i = 0; i < messageSentConvo.length; i++) {
          if (
            newArray[0]?.conversation_id ===
              messageSentConvo[i]?.conversation_id &&
            newArray[0].unread_count === 0
          ) {
            setunReadCount((unReadCount) => unReadCount + 1);
          }
        }

        let index = 0;
        const dummyArray = updatedArray;
        let finalArray = [];
        if (
          urgencyFilter &&
          !urgencyState.includes(newConvo.urgency_level.urgency_level_id)
        ) {
          finalArray = dummyArray;
        } else if (
          statusFilter &&
          !statusState.includes(newConvo.status.status_id)
        ) {
          finalArray = dummyArray;
        } else if (
          hotlineFilter &&
          !hotlineState.includes(newConvo.hotline.hotline_id)
        ) {
          finalArray = dummyArray;
        } else {
          updatedArray.map((value) => {
            if (value?.conversation_id === newConvo?.conversation_id) {
              return (index = updatedArray.indexOf(value));
            }
          });
          dummyArray.splice(index, 1);
          // set up a new conversations' list for an AMWeb user with the latest message on top of that list.
          finalArray = [updatedCount, ...dummyArray];
        }
        return finalArray;
      });
    }
  };

  const filterSocketUrgency = (
    urgencyState,
    newConvo,
    conversations,
    conversationIds
  ) => {
    let totalConversations = [];
    if (urgencyState.includes(newConvo.urgency_level.urgency_level_id)) {
      if (!conversationIds.includes(newConvo.conversation_id)) {
        totalConversations = [...conversations, newConvo];
      } else {
        totalConversations = conversations;
      }
    } else {
      totalConversations = conversations;
    }
    return totalConversations;
  };

  const filterSocketStatus = (
    statusState,
    newConvo,
    conversations,
    conversationIds
  ) => {
    let totalConversations = [];
    if (statusState.includes(newConvo.status.status_id)) {
      if (!conversationIds.includes(newConvo.conversation_id)) {
        totalConversations = [...conversations, newConvo];
      } else {
        totalConversations = conversations;
      }
    } else {
      totalConversations = conversations;
    }
    return totalConversations;
  };

  const filterSocketHotline = (
    hotlineState,
    newConvo,
    conversations,
    conversationIds
  ) => {
    let totalConversations = [];
    if (hotlineState.includes(newConvo.hotline.hotline_id)) {
      if (!conversationIds.includes(newConvo.conversation_id)) {
        totalConversations = [...conversations, newConvo];
      } else {
        totalConversations = conversations;
      }
    } else {
      totalConversations = conversations;
    }
    return totalConversations;
  };

  function arrayUnique(arr, urgencyLevels, status, hotline) {
    setUrgencyLevelData(urgencyLevels);
    setUrgencyLevelStatus(status);
    setUrgencyLevelHotline(hotline);
  }

  const loadFilteredConversations = (
    data,
    type,
    selectedUrgency,
    selectedStatus,
    selectedHotline
  ) => {
    const dataValue = arrayUnique(
      data,
      selectedUrgency,
      selectedStatus,
      selectedHotline
    );
    setConversations(data);
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
  };

  function handleFilterLevel(type, value) {
    setRemoveFilter({ type, value });
  }

  const getConversations = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (user)
      axios
        .get(
          BASE_URL +
            "hotlineResponderConversation/?user_id=" +
            user.user_details.org_user_id,
          options
        )
        .then((res) => {
          let disabledCount = 0;
          setInitialConv(res.data);
          setConversations(res.data);
          const selectedUsers = res.data.reduce(
            (accumulator, currentValue, index) => {
              if (currentValue.unread_count != 0) {
                disabledCount++;
              }
            },
            []
          );
          setunReadCount(disabledCount);
        });
  };

  const onConversationClick = (conversation) => {
    let updatedData = {};
    setConversations((conversations) => {
      updatedData = conversations.map((x) =>
        x.conversation_id === conversation.conversation_id
          ? { ...conversation, unread_count: 0 }
          : x
      );
      return updatedData;
    });
    if (conversation.unread_count > 0 && unReadCount > 0) {
      const leftCount = unReadCount - 1;
      setunReadCount(leftCount);
    }
    setConversation(conversation);
    setUrgencyLevel(conversation.urgency_level.urgency_level_id);
    setStatus(conversation.status.status_id);
  };

  const setCounter = (conversation) => {
    let receivedConversation = conversation.data;
    for (var i = 0; i < conversations.length - 1; i++) {
      if (
        conversations[i].conversation_id === conversation.data.conversation_id
      ) {
        receivedConversation = {
          ...receivedConversation,
          unread_count: conversations[i].unread_count,
        };
      }
    }
    if (
      receivedConversation.unread_count &&
      receivedConversation.unread_count > 0 &&
      unReadCount > 0
    ) {
      const leftCount = unReadCount - 1;
      setunReadCount(leftCount);
    }
  };

  const onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setUrgencyLevel(event);
        }
      });
  };

  const onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      status_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setStatus(event);
        }
      });
  };

  const createMarkup = (html) => {
    return { __html: html };
  };

  const highlightText = (data, str) => {
    let highlightedObject = {};
    highlightedObject = data
      .filter(
        (item) =>
          item.conversation_id.toLowerCase().includes(str.toLowerCase()) ||
          item.responder_conversation_name
            .toLowerCase()
            .includes(str.toLowerCase()) ||
          item.hotline.name.toLowerCase().includes(str.toLowerCase()) ||
          item.message.toLowerCase().includes(str.toLowerCase())
      )
      .map((item) => {
        let conversation_id = item.conversation_id.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let responder_conversation_name = item.responder_conversation_name.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let hotlineName = item.hotline.name.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let itemMessage = item.message.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        return {
          ...item,
          conversation_id: conversation_id,
          responder_conversation_name: responder_conversation_name,
          message: itemMessage,
          hotline: {
            name: hotlineName,
            hotline_id: item.hotline.hotline_id,
          },
        };
      });
    return highlightedObject;
  };

  const onSearch = (e) => {
    var str = e.trim();
    const statusFil = urgencyLevelStatus.map((status) => {
      status = status.value;
      return status;
    });
    let allCheckedOnes = {
      ...(statusFil.length > 0 && { status_id: statusFil }),
      ...(urgencyLevelData.length > 0 && {
        urgency_level_id: urgencyLevelData.map((ugc) => ugc.value),
      }),
      ...(urgencyLevelHotlines.length > 0 && {
        hotline_id: urgencyLevelHotlines.map((ht) => ht.value),
      }),
    };
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;
    const finalUrl =
      str === ""
        ? baseUrl + "&filter=" + JSON.stringify(allCheckedOnes)
        : baseUrl +
          "&filter=" +
          JSON.stringify(allCheckedOnes) +
          "&search=" +
          str.toString();
    axios.get(finalUrl, options).then((res) => {
      setSearchString(str);
      let filteredData = [];
      if (str.length > 0) {
        filteredData = highlightText(res.data, str);
      } else {
        filteredData = res.data;
      }
      setConversations(filteredData);
    });
  };
  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amWEBUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AmWebSidebar amWEBUser />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">Hotline Messages</h1>
              </Row>
              {initialConv?.length > 0 ? (
                <div className="mid-content p-0">
                  <Row>
                    <Col span={24} lg={6} className="message-section py-4">
                      <Col
                        span={24}
                        className=" d-flex justify-content-between px-3"
                      >
                        <h6>
                          Messages{" "}
                          <Badge className="pl-1" count={unReadCount} />
                        </h6>

                        <FilterPopover
                          searchString={searchString}
                          removeFilter={removeFilter}
                          loadFilteredConversations={loadFilteredConversations}
                        />
                      </Col>

                      <Col span={24} className="mt-4 px-3 d-block">
                        <Scrollbars
                          // onScroll={handleScroll}
                          // onScrollFrame={handleScrollFrame}
                          // onScrollStart={handleScrollStart}
                          // onScrollStop={handleScrollStop}
                          // onUpdate={handleUpdate}
                          // renderView={renderView}
                          // renderTrackHorizontal={renderTrackHorizontal}
                          // renderTrackVertical={renderTrackVertical}
                          // renderThumbHorizontal={renderThumbHorizontal}
                          // renderThumbVertical={renderThumbVertical}
                          autoHeight
                          autoHeightMin={0}
                          autoHeightMax={150}
                          thumbMinSize={30}
                          universal={true}
                        >
                          <>
                            {urgencyLevelData.map((data, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  key={index}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyLevel",
                                      data.value
                                    )
                                  }
                                  className="mb-2"
                                >
                                  Urgency: <span>{data.label}</span>
                                </Tag>
                              </>
                            ))}
                            {urgencyLevelHotlines.map((hotline, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyHotline",
                                      hotline.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Hotline: <span>{hotline.label} </span>
                                </Tag>
                              </>
                            ))}
                            {urgencyLevelStatus.map((status, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    handleFilterLevel(
                                      "urgencyStatus",
                                      status.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Status: <span>{status.label} </span>
                                </Tag>
                              </>
                            ))}
                          </>
                        </Scrollbars>
                      </Col>
                      <Col span={24} className="search-box mt-3 px-3">
                        <Form form={form}>
                          <Form.Item name="search" id="search">
                            <Search
                              name="search"
                              id="search"
                              placeholder="Search"
                              onChange={(e) => onSearch(e.target.value)}
                              onSearch={onSearch}
                              autoComplete="off"
                            />
                          </Form.Item>
                        </Form>
                      </Col>
                      <Scrollbars
                        // onScroll={handleScroll}
                        // onScrollFrame={handleScrollFrame}
                        // onScrollStart={handleScrollStart}
                        // onScrollStop={handleScrollStop}
                        // onUpdate={handleUpdate}
                        // renderView={renderView}
                        // renderTrackHorizontal={renderTrackHorizontal}
                        // renderTrackVertical={renderTrackVertical}
                        // renderThumbHorizontal={renderThumbHorizontal}
                        // renderThumbVertical={renderThumbVertical}
                        autoHeight
                        autoHeightMin={0}
                        autoHeightMax={450}
                        thumbMinSize={30}
                        universal={true}
                      >
                        <Col span={24} className=" inbox-messages">
                          {conversations.length > 0 ? (
                            conversations.map((item) => {
                              return (
                                <div
                                  className={
                                    conversation.conversation_id ===
                                    item.conversation_id
                                      ? "msg-wrapper active"
                                      : "msg-wrapper"
                                  }
                                  onClick={() => onConversationClick(item)}
                                >
                                  <div className="d-flex justify-content-between">
                                    <p
                                      ellipsis
                                      style={{ fontWeight: "bold" }}
                                      className="w-75 pr-2 mb-1"
                                      dangerouslySetInnerHTML={createMarkup(
                                        item.responder_conversation_name
                                      )}
                                    />
                                    <span className="small">
                                      {" "}
                                      {moment(item.created_on).isSame(
                                        moment(),
                                        "day"
                                      )
                                        ? moment(item.created_on)
                                            .utc()
                                            .local()
                                            .format("hh:mm")
                                        : moment(item.created_on)
                                            .utc()
                                            .local()
                                            .format("MM:DD:YY")}
                                    </span>
                                  </div>
                                  <div className="d-flex justify-content-between">
                                    <Paragraph
                                      ellipsis
                                      className="font-weight-normal mb-1 sm para_wrapper"
                                    >
                                      <span
                                        //style={{ fontWeight: "bold" }}
                                        className="ellipsisText"
                                        dangerouslySetInnerHTML={createMarkup(
                                          item.hotline.name
                                        )}
                                      />
                                      &nbsp; |&nbsp;
                                      <span
                                       // style={{ fontWeight: "bold" }}
                                        className="ellipsisText"
                                        dangerouslySetInnerHTML={createMarkup(
                                          item.conversation_id
                                        )}
                                      />
                                    </Paragraph>
                                    <span className="small">
                                      {" "}
                                      <Badge count={item.unread_count} />
                                    </span>
                                  </div>
                                </div>
                              );
                            })
                          ) : (
                            <div className="msg-wrapper">No messages found</div>
                          )}
                        </Col>
                      </Scrollbars>
                    </Col>

                    {conversation ? (
                      <Messages
                        conversation={conversation}
                        urgencyLevel={urgencyLevel}
                        status={status}
                        onChangeUrgencyLevel={onChangeUrgencyLevel}
                        onChangeStatus={onChangeStatus}
                        searchString={searchString}
                        newState={newState}
                        setCounter={setCounter}
                      />
                    ) : (
                      <Col
                        span={24}
                        lg={18}
                        className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                      >
                        <Row className="justify-content-center my-3">
                          <img
                            src={NoHotlines}
                            alt="No Hotlines Selected"
                            style={{ opacity: "0.8" }}
                          />
                        </Row>
                        <h5>No Hotline Selected</h5>
                        <small>Select a hotline to view</small>
                      </Col>
                    )}
                  </Row>
                </div>
              ) : (
                <Col
                  span={24}
                  lg={18}
                  className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                >
                  <Row className="justify-content-center my-3">
                    <img
                      src={CreateHotline}
                      alt="No Hotlines Selected"
                      style={{ opacity: "0.8" }}
                    />
                  </Row>
                  <h5>
                    No conversations are initiated for the hotlines assigned to
                    you
                  </h5>
                  {/* <small>Select a hotline to view</small> */}
                </Col>
              )}
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default HotlineMessages;
