import React from "react";
import Logo from "../../src/assets/images/logo.svg";
import { Row, Col } from "antd";
import AnonymooseUser from "../components/userInfo/UserInfo";

const LoggedinHeader = props => {
  return (
    <>
      <Col span={18} md={18} lg={12}>
        <img src={Logo} alt="Annonymoose Logo" className="pl-md-0 pr-0 pr-md-4 pt-0 pt-md-0 pt-lg-0 logo" />
      </Col>
      <Col span={6} md={6} lg={12} className="d-flex justify-content-end av-wrapper">
        <AnonymooseUser userRole={props}/>
      </Col>
    </>
  );
};

export default LoggedinHeader;
