import React, { useState } from "react";
import { Layout, Menu, Row } from "antd";
import { Link } from "react-router-dom";

//import Icons from '../assets/icons/Icon';
// import {MenuUnfoldOutlined, MenuFoldOutlined}from "@ant-design/icons";
import {
  hotline,
  openline,
  polls,
  manageemp,
  broadcastmsg,
  help,
  info,
} from "../assets/icons/Icon";
const { Sider } = Layout;

const OrgSidebar = props => {

  const [collapsed, setCollapsed] = useState(false)
  const toggle = () => {
  setCollapsed(!collapsed)
  };
  return ( 
     <Sider className="d-none d-lg-block w-md-100 sider-width"
          trigger={null} 
          collapsible
          collapsed={collapsed}>
      <Menu theme="dark" mode="inline" defaultSelectedKeys={["3"]}>
        <Menu.Item
          key="1"
          icon={
            <span className="icon-circle">
              <span className="icon icon-hotlines" />
            </span>
          }
        >
          {" "}
          <Link to="/hotlinedirectory">Hotlines</Link>
        </Menu.Item>
        <Menu.Item
          key="2"
          icon={
            <span className="icon-circle">
              <span className="icon icon-openlines" />
            </span>
          }
        >
          {" "}
          Openlines
        </Menu.Item>
        {/* <Menu.Item
          key="3"
          icon={
            <span className="icon-circle">
              <span className="icon icon-polls" />
            </span>
          }
        >
          {" "}
          Polls
        </Menu.Item> */}
        <Menu.Item
          key="3"
          icon={
            <span className="icon-circle">
              <span className="icon icon-manage-emp" />
            </span>
          }
        >
          {" "}
          <Link to="/employee">Manage Employees</Link>
        </Menu.Item>
        <Menu.Item
          key="4"
          icon={
            <span className="icon-circle">
              <span className="icon icon-broadcast" />
            </span>
          }
        >
          {" "}
          Broadcast Messages
        </Menu.Item>
        <Menu.Item
          key="5"
          icon={
            <span className="icon-circle">
              <span className="icon icon-help" />
            </span>
          }
        >
          {" "}
          Help
        </Menu.Item>
        <Menu.Item
          key="6"
          icon={
            <span className="icon-circle">
              <span className="icon icon-info" />
            </span>
          }
        >
          {" "}
          Info
        </Menu.Item>
      </Menu>
      <div className="btm-div">
        <a href="#" className="px-2">
          Info
        </a>
        <a href="#" className="px-2">
          Help
        </a>
      </div>
    </Sider>
    //  React.createElement(
    //   collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,{
    //     className: "trigger d-lg-none d-block",
    //     onClick: {toggle},
    //   }
    // )
  );
};

export default OrgSidebar;
