//Authentication service for AnonyMoose
import axios from "axios";
import { BASE_URL } from "../userconfig";
import * as AppStorage from '../utils/AppStorage';
const options = {
  headers: {
    Accept: "application/json, text/plain",
    "Content-Type": "application/json;charset=UTF-8",
  },
};

class AuthService {
  let;
  async login(email, password, page) {
    const response = await axios.post(
      BASE_URL + "authenticate/",
      { email, password, page },
      options
    );
    if (response.data) {
      AppStorage.set("user", JSON.stringify(response.data));
    }
    return response.data;
  }

  logout() {
    const commonForAllUsers = JSON.parse(AppStorage.get("user"));
    const isamcc = commonForAllUsers.user_data.is_amcc_user;
    const isorgcc = commonForAllUsers.user_data.is_org_cc_user;
    const isamweb = commonForAllUsers.user_data.is_am_web_user;

    let redirectURL = "";
    if (isamcc == true) {
      redirectURL = "amcc";
    } else if (isorgcc == true) {
      redirectURL = "orgcc";
    } else if (isamweb == true) {
      redirectURL = "amweb";
    }

    AppStorage.remove("user");
    AppStorage.remove("userType");
    window.location.reload();
   
  }
}

export default new AuthService();
