import React from "react";
import { Layout, Row, Col, Input, Select } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { connect } from "react-redux";
import { getManageEmpAsync } from "../../actions/manageEmp";
import { AudioOutlined } from "@ant-design/icons";
import GridPopover from "../table/Popover";
import OrgSidebar from "../../containers/OrgSidebar";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);


const { Option } = Select;
const { Content } = Layout;

class ManageEmp extends React.Component {
  constructor(props) {
    super(props);
    const columns = [
      {
        title: "First Name",
        dataIndex: "firstname",
        key: "firstname",
        width: "22%",
        
      },
      {
        title: "Last Name",
        dataIndex: "lastname",
        key: "lastname",
        width: "22%",
      },
      {
        title: "Mobile Number",
        dataIndex: "Mobile_Number",
        key: "Mobile_Number",
        width: "21%",
      },
      // { title: "Access", dataIndex: ["Access","am_web"], key: "am_web ", width: "21%" },
      {
        title: "Access",
        dataIndex: "Access",
        render: (res) => {
          let test = "";
          for (let property in res) {
            if (res[property] == true) {

              if (property == "am_web") {
                property = "AM WEB";
              }
              if(property=="org_cc_user"){
                property = "Org CC"
              }
              if (property == "app_user") {
                property = "APP";
              }
              
              if (test.length == 0) test += property;
              else test += ", " + property;
              //test += property + " ";
            }
          }

          return <span>{test}</span>;
        },
        width: "20%",
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status",
        width: "10%",
        render: () => (
          <div className="primary-tag-rounded tag">
            <span className="circle mr-1"></span>Active
          </div>
        ),
        // render: () => <div className="secondary-tag-rounded tag">Inactive</div>,
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: () => <GridPopover />,
      },
    ];
    this.state = {
      columns: columns,
      data: [],
    };
  }
  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(getManageEmpAsync());
  }
  UNSAFE_componentWillReceiveProps(props) {
    let { manageEmpData } = props;
    this.setState({ data: manageEmpData }, () => {
      this.onSearch("");
    });
  }
  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].includes(searchKey);
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });
  };
  render() {
    return (
      <>
        <Row className="inner-header">
        <LoggedinHeader amORGUser/>
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
            <OrgSidebar amORGUser/>
              <Content className="">
                <Row className="py-3">
                  <h1>Manage Employees</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3">
                    <Col span={18} className="search-row">
                      <Row>
                        <Col span={9} className="search-box">
                          <Search
                          disabled={true}
                            placeholder="Search"
                            onSearch={this.onSearch}
                          />
                        </Col>
                        <Col span={8} className="ml-4">
                          <Select
                            className="w-75"
                            defaultValue="Permissions"
                            disabled
                          >
                            <Option value="1" defaultValue>
                              1
                            </Option>
                            <Option value="2">2</Option>
                          </Select>
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Buttons value="Add Employee" />
                    </Col>
                  </Row>
                </Col>
                <Grid
                  rowKey="unique_identifier"
                  columns={this.state.columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}
function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message,
  };
}

export default connect(mapStateToProps)(ManageEmp);
